body{
    background-color: burlywood;
}

#wrapper{
    border: 2px solid black;
    width: 400px;
    height: 450px;
    margin: auto;
    margin-top: 10%;
    background-color: black;
    border-radius: 10px;
    padding: 10px;
    
}

#wrapper>h2{
    display: flex;
    justify-content: center;
}
h2{
    color: #ffeba7;
}

input{
    /* text-decoration: none; */
    border-radius: 5px;
    border: 2px solid black;
    margin-top: 30px ;
    width: 250px;
    height: 2.5em;
    padding-left: 10px;
}

/* input:active{
    border: none;
} */

button{
    /* position: absolute; */
    border-radius: 5px;
    border: none;
    margin-top: 30px ;
    width: 105px;
    height: 44px;
    margin-left: 20px;
    color: black;
    font-weight: 600;
    background-color: #ffeba7;
}
button:hover{
    cursor: pointer;
}
#todolist{
    color: white;
    font-size: 28px;
    margin-top: 10px ;
    overflow-y: scroll;
    height: 300px;
}

#todolist>p{
    margin: 10px 0px;
}
#todolist>p:hover{
    cursor: pointer;
}




